.aboutus{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 2vw;
    height: 200vh;
    width: 100%;
    background-color: #66b2ff;
}

.equipe, .valeurs, .valeurs-image, .engagement, .engagement-image{
    height: auto;
    width: auto;
}

.equipe, .valeurs, .engagement{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.equipe-image, .valeurs-image, .engagement-image{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.equipe{
    justify-content: space-between;
    grid-area: 1 / 1 / 2 / 3; 
}

.equipe > h2{
    height: auto;
    margin: 0;
    font-size: clamp(40px, 3vw, 90px);
    color: white;
}

.equipe > p{
    height: auto;
    width:  70%;
    margin: 0;
    margin-left: 1vw;
    margin-right: 1vw;
    padding: 1.5vw;
    font-size: clamp(15px, 1vw, 50px);
    color: rgb(0, 0, 0);
}

.equipe > .assistantes{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 70%;
    width: 70%;
}

.assistante1, .assistante2{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 25%;
    height: 80%;
    padding: 2.5%;
    border-radius: 16px;
    border-bottom: 2px solid white;
    border-top: 2px solid white;
}

.photo_de_profile{
    height: 10vw;
    width: 10vw;
    border-radius: 50%;
}

.assistante1 > .photo_de_profile{
    background-image: url("../../assets/valoche.svg");
    background-position: center;
    background-size: cover;
}

.assistante2 > .photo_de_profile{
    background-image: url("../../assets/valoche.svg");
    background-position: center;
    background-size: cover;
}

.assistante1 > h3, .assistante2 > h3 {
    height: auto;
    margin: 0;
    font-size: clamp(25px, 1.5vw, 90px);
}

.assistante1 > p,.assistante2 > p {
    height: auto;
    margin: 0;
    margin-left: 5%;
    margin-right: 5%;
    font-size: clamp(12.5px, 1vw, 90px);
}

.valeurs{
    grid-area: 2 / 1 / 3 / 2;
    background-color: white;
}

.valeurs-image{
    grid-area: 2 / 2 / 3 / 3;
    background-image: url("../../assets/fond.jpg");
}

.engagement{
    grid-area: 3 / 2 / 4 / 3;
}

.engagement-image{
    grid-area: 3 / 1 / 4 / 2;
    background-image: url("../../assets/fond.jpg");
}

/* Pour les écrans de mobile (max-width: 600px) */
@media (max-width: 600px) {
    .aboutus{
        height: 300vh;
    }
    .equipe > .assistantes{
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        height: 80%;
    }
    .assistante1, .assistante2{
        height: 45%;
        width: 70%;
    }
    .photo_de_profile{
        width: 30vw;
        height: 30vw;
    }
}

