.home {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: auto;
    width: 100%;
    min-height: 80vh;
    text-align: center;
    background-image: url("../../assets/fond.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}


.title_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40vw;
    width: 100%;
    z-index: 1;
}

.title {
    width: 70%;
    font-size: clamp(35px, 5vw, 150px);
    white-space: warp;
}

.title_image_left_container, .title_image_right_container{
    width: 15%;
    height: 80%;
}

.title_image_left, .title_image_right{
    position: relative;
    display: grid;
    grid-template-columns: repeat(13, 1fr);
    grid-template-rows: repeat(18, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    width: 150%;
    height: 100%;
} 

.title_image_left{
    left: -50%;
}

.title_image_right{
    right: -20%;
}

.title_image_left > .image, .title_image_right > .image {
    width: auto;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 8px;
}

.title_image_left > .image:nth-child(1){
    grid-area: 6 / 7 / 12 / 13;
    background-image: url(../../assets/fond.jpg);
}
.title_image_left > .image:nth-child(2){
    grid-area: 13 / 9 / 18 / 14;
    background-image: url(../../assets/fond.jpg);
}
.title_image_left > .image:nth-child(3){
    grid-area: 13 / 2 / 19 / 8;
    background-image: url(../../assets/fond.jpg);
}
.title_image_left > .image:nth-child(4){
    grid-area: 8 / 2 / 12 / 6;
    background-image: url(../../assets/fond.jpg);
}
.title_image_left > .image:nth-child(5){
    grid-area: 1 / 7 / 5 / 11;
    background-image: url(../../assets/fond.jpg);
}
.title_image_left > .image:nth-child(6){
    grid-area: 2 / 1 / 7 / 6;
    background-image: url(../../assets/fond.jpg);
}





.title_image_right > .image:nth-child(1){
    grid-area: 9 / 1 / 14 / 6;
    background-image: url(../../assets/fond.jpg);
}
.title_image_right > .image:nth-child(2){
    grid-area: 13 / 7 / 19 / 13;
    background-image: url(../../assets/fond.jpg);
}
.title_image_right > .image:nth-child(3){
    grid-area: 5 / 3 / 8 / 6;
    background-image: url(../../assets/fond.jpg);
}
.title_image_right > .image:nth-child(4){
    grid-area: 8 / 7 / 12 / 11;
    background-image: url(../../assets/fond.jpg);
}
.title_image_right > .image:nth-child(5){
    grid-area: 2 / 7 / 7 / 12;
    background-image: url(../../assets/fond.jpg);
}

.wave {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 20vw;
    overflow: hidden;
    z-index: 10;
}

.wave-svg {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}

.wave-svg-bis{
    position: absolute;
    bottom: 0;
    left: 99.9%;
    width: 100%;
}

@media (max-width: 600px) {
    .home{
        min-height: 30vh;
    }
    .title{
        width: 100%;
        z-index: 10;
    }
    .title_image_left_container, .title_image_right_container{
        width:  50%;
        height: 100%;
        position: absolute;
        z-index: 0;
    }
    .title_image_left, .title_image_right{
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.25;
        z-index: -1;
    }
    .title_image_left_container{
        top: 0;
        left: -10%;
    }
    .title_image_right_container{
        top: 0;
        right: -10%;
    }
}

